
@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@500;700&display=swap');
:root{

    --Desaturated-Dark-Cyan: hsl(180, 29%, 50%)
    /* ### Neutral */
    --Light-Grayish-Cyan-Background: hsl(180, 52%, 96%)
    --Light-Grayish-Cyan: hsl(180, 31%, 95%) /*(Filter Tablets)*/
    --Dark-Grayish-Cyan: hsl(180, 8%, 52%)
    --Very-Dark Grayish-Cyan: hsl(180, 14%, 20%)

}

*{
    font-family: 'League Spartan', sans-serif;
    font-size: 15px;
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
body{
    background-color: hsl(180, 52%, 96%);
}

h1{
    color: hsl(180, 14%, 20%);
    font-size: 1.5rem;
}
a{
    text-decoration: none;
    color: hsl(180, 8%, 52%);
}
a:hover{
    color: hsl(180, 29%, 50%);
    text-decoration: underline;
}

.header-image{
    background-color: hsl(180, 29%, 50%);
}
.background-image{
    width: 100%;
    height: 100%;
    
}

/* buttons */
.btn-clear{
    background-color: #fff;
    color: hsl(180, 8%, 52%);
    border: none;
    
}
.btn-clear:hover{
    color: hsl(180, 29%, 50%);
    border-bottom: .15em solid hsl(180, 29%, 50%);
}

.tag{
    background-color: hsl(180, 52%, 96%);
    border: none;
    font-size: .6rem;
    margin: 0 .5em;
    height: 2em;
    color: hsl(180, 29%, 50%);
    font-weight: 700; 
    font-size: .8rem;
    padding: .5em;  
}
.filter-button {
    display: flex;
    height: 2em;
    padding: 0;
    background: hsl(180, 57%, 90%);
    border: none;
    outline: none;
    overflow: hidden;
    font-size: 1rem;
    font-weight: 500;
    color: hsl(180, 29%, 50%);
  }

  .button__text,
  .button__icon {
    display: inline-flex;
    align-items: center;
    padding: 0 0.5em;
    height: 100%;
  }
  
  .button__icon {
    font-size: 1.5em;
    background: hsl(180, 29%, 50%);
  }
  .button__icon:hover{
    background-color: black;
  }
  

.new{
    height: 2em;
    font-size: 0.8rem;
    padding: .6em;
    margin: 0 .4em;
    background-color: hsl(180, 29%, 50%);
    border: none;
    border-radius: 1em;
    color: #fff;   
}
.featured{
    height: 2em;
    font-size: 0.8rem;
    padding: .6em;
    margin: 0 .6em;
    background-color: hsl(0, 0%, 0%);
    border: none;
    border-radius: 1em;
    color: #fff;

}
.name{
    color: hsl(180, 29%, 50%);
    font-size: 1.2rem;
}
.container{
    max-width: 85%;
    margin: 0 auto;;
}
.filter{
    background-color:#fff;
    position: relative;
    z-index: 999;
    margin-top: -20px;
    height: 40px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2em;
    /* display: none; */
}

.job{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    margin: 4em 0;
    padding: 2em 2.5em;
}
.div-shadow {
    box-shadow: 10px 20px 30px -15px hsl(180, 29%, 50%);
}
.job-info{
    display: flex;
    
}
ul{
    display: flex;
    justify-content: space-between;
    color: hsl(180, 8%, 52%);
}
.days{
    list-style: none;
    
}
li{
    font-size: .8rem;
}
span{
    font-size: 1rem;
}
.time, .location{
    margin-left: 1.5em;
}
  
.post-info{
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: space-between;
    margin-left: 1em;
}
.new{
    margin-left: 2em;
}
    
.company-name, .job-title, .job-criteria{
    margin: 0;
} 
.company-name{
    display: flex;
    align-items: center;
    
}
.job-tags{
    display: flex;
}

.tag:hover{
    color: #fff;
    background-color: hsl(180, 29%, 50%);
}






.attribution a { 
    color: hsl(228, 45%, 44%);
    font-size: 11px; text-align: center;
}
